.listas-container {
    width: 100%;
    max-width: 1180px;
    padding: 0 30px;
    margin: 32px auto;
}

.listas-container header {
    display: flex;
    align-items: center;
}

.listas-container header span{
    font-size: 20px;
    margin-left: 24px;
}

.listas-container header button {
    height: 60px;
    width: 60px;
    border-radius: 4px;
    border: 1px solid #dcdce6;
    background:  transparent;
    margin-left: auto;
    transition: border-color 0.2s;
}

.listas-container header button:hover  {
    border-color: #999;
}

.listas-container h1 {
    margin-top: 80px;
    margin-bottom: 24px;
}

.listas-container ul{
    display: grid;
    grid-template-columns:  repeat(1, 1fr);
    grid-gap: 24px;
    list-style: none;
}

.listas-container ul li {
    background: #f0f0f5;
    padding: 12px;
    position: relative;
    box-shadow: 0 0 100px rgba(0, 0, 0, 0.2);
    border: 2px solid #f0f0f5;
    border-radius: 8px;
    transition: border-color 0.3s;
}

.listas-container ul li:hover {
    border-color: #416284;
}

.listas-container ul li button {
    position: absolute;
    right: 24px;
    top: 24px;
    border: 0;
}

.listas-container ul li button:hover{
    opacity: 0.7;
}

.listas-container ul li strong {
    display: block;
    margin-bottom: 16px;
    color: #41414d;
}

.listas-container ul li p + strong {
    margin-top: 32px;
}

.listas-container ul li p {
    color: #737380;
    line-height: 21px;
    font-size: 16px;
}

